import React, { useEffect, useState } from "react"
import http from 'axios'
import Team from './components/Team'

const App = () => {
  const [teams, setTeams] = useState(false)
  const [filter, setFilter] = useState("")

  useEffect(() => {
    const init = async () =>{
      const response = await http("https://demoapi.com/api/teams")
      setTeams(response.data)
    }
    init()
  }, [])
  return (
    <>
      <h1>NBA teams - all star voting</h1>
      <input type="text" name="filter" value={filter} onChange={(e) => setFilter(e.target.value)} placeholder="filter" />
      { teams && 
          teams.filter((team) => {
            return team.franchisePlayers.filter((player) => player.name.includes(filter)).length > 0
          }).map((team, index) => <Team team={team} key={index} />)}
    </>
  )
}

export default App
