import React, { useState } from 'react'
import http from 'axios'

const Team = ({team}) => {
    const [showMore, setShowMore] = useState(false)
    const [disableVote, setDisableVote] = useState(false)
    const [voted, setVoted] = useState(false)

    const vote = async (e, id) => {
        setDisableVote(true)
        try {
            const response = await http.post("https://demoapi.com/api/vote",{
                id: id
            })
            setVoted(id)
            setDisableVote(false)
        } catch (error) {
            console.log(error)
            setDisableVote(false)
        }
    }
    return (
        <>
        <h2>{team.name}</h2>
        <button onClick={() => setShowMore(!showMore)}>{ showMore ? "show less" : "show more"}</button>
        { showMore && 
            <ul>
                { team.franchisePlayers.map((player, index) => (
                    <li key={player.id}>
                        <h3>{player.name}</h3>
                        <button onClick={(e) => vote(e, player.id)} disabled={disableVote ||player.id === voted}>{player.id === voted ? "voted" : "vote"}</button>
                    </li>
                ))}
            </ul>
         }    
        </>
    )
}

export default Team